.calculator {
    margin: auto;
    width: 300px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border: 1px solid #cccccc;
    border-radius: 10px;
  }
  .display {
    width: 95%;
    height: 10%;
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    text-align: right;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #cccccc;
  }
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    width: 100%;
    height: 70%;
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    text-align: center;
  }
  button {
    background-color: #f0f0f0;
    color: #000000;
    font-size: 24px;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #cccccc;
    cursor: pointer;
  }
  button:hover {
    background-color: #cccccc;
  }